const DefaultXrExperienceContext = createContext<WebXRDefaultExperience | undefined>(undefined);

const useXr = () => useContext(DefaultXrExperienceContext);

type DefaultXrExperienceProps = {
  options?: WebXRDefaultExperienceOptions;
  children?: ReactNode;
};

const WebXrDefaultExperience = memo(
  forwardRef<WebXRDefaultExperience | undefined, DefaultXrExperienceProps>(
    function WebXrDefaultExperience({ options, children }, ref) {
      const scene = useScene();
      const [xrExperience, setXrExperience] = useState<WebXRDefaultExperience>();

      useImperativeHandle(
        ref,
        () => {
          if (!xrExperience) return;
          return xrExperience;
        },
        [xrExperience]
      );

      useEffect(() => {
        if (!scene) return;

        const asyncEffect = (async () => {
          const xr = await scene.createDefaultXRExperienceAsync(options);

          setXrExperience(xr);

          return () => {
            xr.dispose();
          };
        })();

        return () => {
          (async () => {
            (await asyncEffect)();
          })();
        };
      }, [options, scene]);

      return xrExperience ? (
        <DefaultXrExperienceContext.Provider value={xrExperience}>
          {children}
        </DefaultXrExperienceContext.Provider>
      ) : null;
    }
  )
);

type WebXrDomOverlayProps = {
  options: IWebXRDomOverlayOptions;
  version?: string;
};
const WebXrDomOverlay = memo(function WebXrDomOverlay({ version, options }: WebXrDomOverlayProps) {
  const xr = useXr();

  useEffect(() => {
    if (!xr) return;

    const featuresManager = xr.baseExperience.featuresManager;
    const domOverlay = featuresManager.enableFeature(
      WebXRFeatureName.DOM_OVERLAY,
      version,
      options
    ) as WebXRDomOverlay;

    return () => {
      domOverlay.dispose();
    };
  }, [version, options, xr]);

  return null;
});

type WebXxHitTestProps = {
  options?: IWebXRHitTestOptions;
  version?: string;
  onHitTestResultObservable?: (hits: IWebXRHitResult[], eventState: EventState) => void;
};
const WebXrHitTest = memo(
  forwardRef<WebXRHitTest | undefined, WebXxHitTestProps>(function WebXrHitTest(
    { options, version, onHitTestResultObservable },
    ref
  ) {
    const xr = useXr();
    const [webXrHitTest, setWebXrHitTest] = useState<WebXRHitTest>();

    useImperativeHandle(
      ref,
      () => {
        if (!webXrHitTest) return;
        return webXrHitTest;
      },
      [webXrHitTest]
    );

    useEffect(() => {
      if (!xr) return;

      const featuresManager = xr.baseExperience.featuresManager;
      const hitTest = featuresManager.enableFeature(
        WebXRFeatureName.HIT_TEST,
        version,
        options
      ) as WebXRHitTest;

      setWebXrHitTest(hitTest);

      return () => {
        hitTest.dispose();
        setWebXrHitTest(undefined);
      };
    }, [options, version, xr]);

    useEffect(() => {
      if (!webXrHitTest || !onHitTestResultObservable) return;

      const observer = webXrHitTest.onHitTestResultObservable.add(onHitTestResultObservable);

      return () => {
        webXrHitTest.onHitTestResultObservable.remove(observer);
      };
    }, [webXrHitTest, onHitTestResultObservable]);

    return null;
  })
);

type WebXrAnchorSystemProps = {
  options?: IWebXRAnchorSystemOptions;
  version?: string;
  onAnchorAddedObservable?: (anchor: IWebXRAnchor, eventState: EventState) => void;
  onAnchorRemovedObservable?: (anchor: IWebXRAnchor, eventState: EventState) => void;
  onAnchorUpdatedObservable?: (anchor: IWebXRAnchor, eventState: EventState) => void;
};
const WebXrAnchorSystem = memo(
  forwardRef<WebXRAnchorSystem | undefined, WebXrAnchorSystemProps>(function WebXrAnchorSystem(
    {
      version,
      options,
      onAnchorAddedObservable,
      onAnchorRemovedObservable,
      onAnchorUpdatedObservable,
    },
    ref
  ) {
    const xr = useXr();
    const [webXRAnchorSystem, setWebXRAnchorSystem] = useState<WebXRAnchorSystem>();

    useImperativeHandle(
      ref,
      () => {
        if (!webXRAnchorSystem) return;
        return webXRAnchorSystem;
      },
      [webXRAnchorSystem]
    );

    useEffect(() => {
      if (!xr) return;

      const featuresManager = xr.baseExperience.featuresManager;
      const anchorSystem = featuresManager.enableFeature(
        WebXRFeatureName.ANCHOR_SYSTEM,
        version,
        options
      ) as WebXRAnchorSystem;

      setWebXRAnchorSystem(anchorSystem);

      return () => {
        anchorSystem.dispose();
        setWebXRAnchorSystem(undefined);
      };
    }, [options, version, xr]);

    const useWebXRAnchorSystemObserver = <
      K extends {
        [K in keyof WebXRAnchorSystem]: WebXRAnchorSystem[K] extends Observable<any> ? K : never;
      }[keyof WebXRAnchorSystem],
      T extends WebXRAnchorSystem[K] extends Observable<infer U> ? U : never,
      C extends (eventData: T, eventState: EventState) => void
    >(
      eventName: K,
      callback?: C,
      _webXRAnchorSystem = webXRAnchorSystem
    ) => {
      useEffect(() => {
        if (!_webXRAnchorSystem || !callback) return;

        const observable = _webXRAnchorSystem[eventName] as unknown as Observable<T>;
        const observer = observable.add(callback);

        return () => {
          observable.remove(observer);
        };
      }, [_webXRAnchorSystem, eventName, callback]);
    };

    useWebXRAnchorSystemObserver('onAnchorAddedObservable', onAnchorAddedObservable);
    useWebXRAnchorSystemObserver('onAnchorRemovedObservable', onAnchorRemovedObservable);
    useWebXRAnchorSystemObserver('onAnchorUpdatedObservable', onAnchorUpdatedObservable);

    return null;
  })
);

type WebXrImageTrackingProps = {
  options: IWebXRImageTrackingOptions;
  version?: string;
  onUntrackableImageFoundObservable?: (index: number, eventState: EventState) => void;
  onTrackableImageFoundObservable?: (
    trackedImage: IWebXRTrackedImage,
    eventState: EventState
  ) => void;
  onTrackedImageUpdatedObservable?: (
    trackedImage: IWebXRTrackedImage,
    eventState: EventState
  ) => void;
};

const WebXrImageTracking = memo(function WebXrImageTracking({
  version,
  options,
  onUntrackableImageFoundObservable,
  onTrackableImageFoundObservable,
  onTrackedImageUpdatedObservable,
}: WebXrImageTrackingProps) {
  const xr = useXr();
  const [imageTracking, setImageTracking] = useState<WebXRImageTracking>();

  useEffect(() => {
    if (!xr) return;

    const featuresManager = xr.baseExperience.featuresManager;
    const imageTracking = featuresManager.enableFeature(
      WebXRFeatureName.IMAGE_TRACKING,
      version,
      options
    ) as WebXRImageTracking;

    setImageTracking(imageTracking);

    return () => {
      imageTracking.dispose();
      setImageTracking(undefined);
    };
  }, [version, options, xr]);

  const useImageTrackingObserver = <
    K extends {
      [K in keyof WebXRImageTracking]: WebXRImageTracking[K] extends Observable<any> ? K : never;
    }[keyof WebXRImageTracking],
    T extends WebXRImageTracking[K] extends Observable<infer U> ? U : never,
    C extends (eventData: T, eventState: EventState) => void
  >(
    eventName: K,
    callback?: C,
    _imageTracking = imageTracking
  ) => {
    useEffect(() => {
      if (!_imageTracking || !callback) return;

      const observable = _imageTracking[eventName] as unknown as Observable<T>;
      const observer = observable.add(callback);

      return () => {
        observable.remove(observer);
      };
    }, [_imageTracking, eventName, callback]);
  };

  useImageTrackingObserver('onUntrackableImageFoundObservable', onUntrackableImageFoundObservable);
  useImageTrackingObserver('onTrackableImageFoundObservable', onTrackableImageFoundObservable);
  useImageTrackingObserver('onTrackedImageUpdatedObservable', onTrackedImageUpdatedObservable);

  return null;
});

