const cameraMatrixState = atom<Matrix>({
  key: 'cameraMatrix',
  default: Matrix.Identity(),
});

const xrAnchorSystemState = atom<WebXRAnchorSystem>({
  key: 'xrAnchorSystem',
  default: undefined,
  dangerouslyAllowMutability: true,
});

const xrHitTestState = atom<IWebXRHitResult[]>({
  key: 'xrHitTest',
  default: [],
  dangerouslyAllowMutability: true,
});

type XrThingTrackable = {
  id?: number;
  name: string;
  marker: IWebXRImageTrackingOptions['images'][number];
};

type XrThingRegistered = {
  status: 'registered';
  tracked: IWebXRAnchor;
};
type XrThingUnregistered = {
  status: 'unregistered';
  tracked: IWebXRTrackedImage;
};

type XrThing = XrThingTrackable & (XrThingRegistered | XrThingUnregistered);

const xrThingTrackablesState = atom<XrThingTrackable[]>({
  key: 'xrThingTrackables',
  default: [
    {
      name: 'Air Conditioner',
      marker: {
        src: acMarker,
        estimatedRealWorldWidth: 0.127,
      },
    },
    {
      name: 'Light',
      marker: {
        src: lightMarker,
        estimatedRealWorldWidth: 0.127,
      },
    },
  ],
});

const _trackedImageState = atomFamily<
  {
    /** Cache busting for IWebXRTrackedImage instance */
    timestamp: Date;
    tracked: IWebXRTrackedImage;
  },
  number
>({
  key: '_trackedImage',
  dangerouslyAllowMutability: true, // 'xrTrackingResult' self updates
});

const _trackedImageIdsState = atom<number[]>({
  key: '_trackedImageIds',
  default: [],
});

const trackedImageState = selectorFamily<IWebXRTrackedImage, number>({
  key: 'trackedImage',
  get:
    (id) =>
    ({ get }) =>
      get(_trackedImageState(id)).tracked,
  set:
    (id) =>
    ({ set, reset }, newValue) => {
      if (newValue instanceof DefaultValue) {
        reset(_trackedImageState(id));
        set(_trackedImageIdsState, (prevIds) => prevIds.filter((_) => _ != id));
      } else {
        set(_trackedImageIdsState, (prevIds) =>
          prevIds.indexOf(id) === -1 ? [...prevIds, id] : prevIds
        );
        set(_trackedImageState(id), {
          timestamp: new Date(),
          tracked: newValue,
        });
      }
    },
  dangerouslyAllowMutability: true, // 'xrTrackingResult' self updates
});

const trackedImagesState = selector<IWebXRTrackedImage[]>({
  key: 'trackedImages',
  get: ({ get }) => get(_trackedImageIdsState).map((id) => get(_trackedImageState(id)).tracked),
  // set: ({ get, set, reset }, newValue) => {
  //   if (newValue instanceof DefaultValue) {
  //     get(_trackedImageIdsState).forEach((id) => reset(_trackedImageState(id)));
  //     reset(_trackedImageIdsState);
  //   } else {
  //     set(
  //       _trackedImageIdsState,
  //       newValue.map((t) => t.id)
  //     );
  //     newValue.forEach((t) => set(_trackedImageState(t.id), t));
  //   }
  // },
  dangerouslyAllowMutability: true, // 'xrTrackingResult' self updates
});

const _anchorState = atomFamily<IWebXRAnchor, number>({
  key: '_anchor',
  dangerouslyAllowMutability: true, // 'IWebXRAnchor' self updates
});

const _anchorIdsState = atom<Map<number, number | undefined>>({
  key: '_anchorIds',
  default: new Map(),
});

const anchorState = selectorFamily<IWebXRAnchor, number>({
  key: 'anchor',
  get:
    (id) =>
    ({ get }) =>
      get(_anchorState(id)),
  set:
    (id) =>
    ({ set, reset }, newValue) => {
      if (newValue instanceof DefaultValue) {
        reset(_anchorState(id));
        set(_anchorIdsState, (prevIds) => {
          const newIds = new Map(prevIds);
          newIds.delete(id);
          return newIds;
        });
      } else {
        set(_anchorIdsState, (prevIds) => {
          if (prevIds.has(id)) return prevIds;
          const newIds = new Map(prevIds);
          newIds.set(id, undefined);
          return newIds;
        });
        set(_anchorState(id), newValue);
      }
    },
  dangerouslyAllowMutability: true, // 'IWebXRAnchor' self updates
});

const anchorsState = selector<IWebXRAnchor[]>({
  key: 'anchors',
  get: ({ get }) => Array.from(get(_anchorIdsState)).map(([id]) => get(_anchorState(id))),
  dangerouslyAllowMutability: true, // 'IWebXRAnchor' self updates
});

const _xrThingState = atomFamily<XrThing, number>({
  key: '_xrThing',
});

const _xrThingIdsState = atom<Set<number>>({
  key: '_xrThingIds',
  default: new Set(),
});

const xrThingState = selectorFamily<XrThing, number>({
  key: 'xrThing',
  get:
    (id) =>
    ({ get }) =>
      get(_xrThingState(id)),
  set:
    (id) =>
    ({ set, reset }, newValue) => {
      if (newValue instanceof DefaultValue) {
        reset(_xrThingState(id));
        set(_xrThingIdsState, (prevIds) => {
          const newIds = new Set(prevIds);
          newIds.delete(id);
          return newIds;
        });
      } else {
        set(_xrThingIdsState, (prevIds) => {
          const newIds = new Set(prevIds);
          newIds.add(id);
          return newIds;
        });
        set(_xrThingState(id), newValue);
      }
    },
});

const xrThingsState = selector<XrThing[]>({
  key: 'xrThings',
  get: ({ get }) => {
    const trackables = get(xrThingTrackablesState);
    const unregistered: XrThingUnregistered[] = Object.values(get(trackedImagesState)).map((t) => ({
      status: 'unregistered',
      tracked: t,
    }));
    const registeredIds = get(_anchorIdsState);
    const registered: XrThingRegistered[] = Object.values(get(anchorsState)).map((t) => ({
      status: 'registered',
      tracked: t,
    }));
    return [
      ...registered,
      ...unregistered.filter((u) => !registeredIds.has(u.tracked.id) && !u.tracked.emulated),
    ].map((u) => {
      const id = u.status === 'registered' ? registeredIds.get(u.tracked.id) : u.tracked.id;
      const trackable =
        id === undefined
          ? {
              name: 'Unknown',
            }
          : trackables[id];
      return {
        id: id,
        name: trackable.name,
        ...u,
        marker: trackable,
      };
    });
  },
  dangerouslyAllowMutability: true, // 'xrTrackingResult' self updates
});

const xrThingsSortedState = selector<XrThing[]>({
  key: 'xrThingsSorted',
  get: ({ get }) => {
    const xrThings = get(xrThingsState);
    const cameraMatrix = get(cameraMatrixState);
    return xrThings
      .map((thing) => {
        const thingPosition = thing.tracked.transformationMatrix.getTranslation();
        const cameraPosition = cameraMatrix.getTranslation();
        const cameraForward = new Vector3(cameraMatrix.m[8], cameraMatrix.m[9], cameraMatrix.m[10]);
        const distance = Vector3.Distance(thingPosition, cameraPosition);
        const angle = Vector3.Dot(
          cameraForward,
          thingPosition.subtract(cameraPosition).normalize()
        );

        return { thing, distance, angle };
      })
      .sort((a, b) => b.angle - a.angle ?? a.distance - b.distance)
      .map((_) => _.thing);
  },
  dangerouslyAllowMutability: true, // 'xrTrackingResult' self updates
});

const EmptyThings = memo(function EmptyThings() {
  return (
    <>
      <p>등록된 기기가 없습니다</p>
      <Icon shape="Search" width={64} height={64} />
      <Button
        style={{
          alignSelf: 'stretch',
        }}
        title="인식표를 스캔해 주세요"
        status="disabled"
      />
    </>
  );
});

const AskForRegistration = memo(function AskForRegistration({
  thing,
  isAvailable,
  onRegister,
}: {
  thing: XrThing;
  isAvailable: boolean;
  onRegister: Function;
}) {
  return (
    <>
      <p>새로운 기기를 등록할까요?</p>
      <h1>{thing.name}</h1>
      <Button
        style={{
          alignSelf: 'stretch',
        }}
        title={isAvailable ? '등록' : '위치 인식 중'}
        onPress={() => onRegister(thing)}
        status={isAvailable ? 'normal' : 'disabled'}
      />
    </>
  );
});

const ControlPanel = memo(
  function ControlPanel({ thing }: { thing: XrThing }) {
    switch (thing.name) {
      case 'Light':
        return (
          <>
            <p>{thing.name}</p>
            <Switch checked={false} theme="orange" />
          </>
        );
      case 'Air Conditioner':
        const temp = Number((20 + Math.sin(Date.now() * 0.001) * 10).toFixed());
        return (
          <>
            <p>{thing.name}</p>
            <p style={{ fontSize: 64 }}>{temp}</p>
            <Slider
              value={temp}
              minimumValue={5}
              maximumValue={35}
              style={{
                alignSelf: 'stretch',
              }}
            />
          </>
        );
      default:
        return (
          <>
            <p>{thing.name}</p>
          </>
        );
    }
  },
  (a, b) => true
);

const BottomSheet = memo(function BottomSheet() {
  const xrAnchorSystem = useRecoilValue(xrAnchorSystemState);
  const xrHitTest = useRecoilValue(xrHitTestState);

  const xrThings = useRecoilValue(xrThingsSortedState);

  const register = useRecoilCallback(
    ({ set }) =>
      async (thing: XrThing) => {
        try {
          const matrix = thing.tracked.transformationMatrix;
          const position = matrix.getTranslation();
          const rotation = Quaternion.FromRotationMatrix(thing.tracked.transformationMatrix);

          // const anchor = await xrAnchorSystem.addAnchorAtPositionAndRotationAsync(position, rotation);
          // HACK: of above, addAnchorAtPositionAndRotationAsync does not work

          const hit = xrHitTest[0];
          if (!hit) return;

          const anchor = await xrAnchorSystem.addAnchorPointUsingHitTestResultAsync(
            hit,
            hit.position.subtract(position),
            hit.rotationQuaternion.subtract(rotation)
          );

          console.log(anchor);

          set(_anchorIdsState, (existing) => {
            const newIds = new Map(existing);
            newIds.set(thing.tracked.id, thing.id);
            return newIds;
          });
        } catch (e) {
          alert(e);
        }
      },
    [xrAnchorSystem, xrHitTest]
  );

  const focusedThing = xrThings[0];

  const Content = memo(function Content({ thing }: { thing?: XrThing }) {
    switch (thing?.status) {
      case undefined:
        return <EmptyThings />;
      case 'unregistered':
        return (
          <AskForRegistration
            thing={thing}
            isAvailable={xrHitTest.length > 0}
            onRegister={register}
          />
        );
      case 'registered':
        return <ControlPanel thing={thing} />;
      default:
        return null;
    }
  });

  return (
    <div id="XrUi" className={styles.bottomSheet}>
      <Content thing={focusedThing} />
    </div>
  );
});

export const AremoteScene = memo(function AremoteScene() {
  const RecoilBridge = useRecoilBridgeAcrossReactRoots_UNSTABLE();

  const XrExperience = memo(function XrExperience() {
    const XrThingTrackables = useRecoilValue(xrThingTrackablesState);
    const setXrHitTest = useSetRecoilState(xrHitTestState);
    const setXrAnchorSystem = useSetRecoilState(xrAnchorSystemState);

    const xrAnchorSystem = useRef<WebXRAnchorSystem>(null);

    const CameraTracker = memo(function CameraTracker() {
      const xr = useXr();
      const setCameraMatrix = useSetRecoilState(cameraMatrixState);

      useEffect(() => {
        if (!xr) return;

        let camera: Camera;
        let cameraObserver: Nullable<Observer<Camera>>;

        const stateObserver = xr.baseExperience.onStateChangedObservable.add((state) => {
          if (state === WebXRState.IN_XR) {
            camera = xr.baseExperience.camera;
            cameraObserver = camera.onViewMatrixChangedObservable.add((camera) => {
              setCameraMatrix(camera.worldMatrixFromCache.clone());
            });
          } else if (cameraObserver) {
            camera.onViewMatrixChangedObservable.remove(cameraObserver);
            setCameraMatrix(Matrix.Identity());
          }
        });

        return () => {
          xr.baseExperience.onStateChangedObservable.remove(stateObserver);
        };
      }, [xr, xr?.baseExperience.state, xr?.baseExperience.camera, setCameraMatrix]);
      return null;
    });

    const updateTrackedImage = useRecoilCallback(
      ({ set }) =>
        (trackedImage: IWebXRTrackedImage) =>
          set(trackedImageState(trackedImage.id), trackedImage),
      []
    );

    const updateAnchor = useRecoilCallback(
      ({ set }) =>
        (anchor: IWebXRAnchor) =>
          set(anchorState(anchor.id), anchor)
    );

    return (
      <WebXrDefaultExperience
        options={{
          uiOptions: {
            sessionMode: 'immersive-ar',
            referenceSpaceType: 'unbounded',
            onError: (e) => {
              alert(e);
            },
          },
        }}>
        <WebXrHitTest onHitTestResultObservable={setXrHitTest} />
        <WebXrImageTracking
          options={{
            images: XrThingTrackables.map((t) => t.marker),
          }}
          onTrackedImageUpdatedObservable={updateTrackedImage}
        />
        <WebXrAnchorSystem
          ref={(r) => {
            if (r) {
              setXrAnchorSystem(r);
              //@ts-expect-error
              xrAnchorSystem.current = r;
            }
          }}
          onAnchorUpdatedObservable={updateAnchor}
        />
        <WebXrDomOverlay
          options={{
            element: '#WebXrUi', // TODO: useRef
          }}
        />
        <CameraTracker />
      </WebXrDefaultExperience>
    );
  });

  const NewDeviceMarker = memo(function NewDeviceMarker(
    props: PropsWithRef<JSX.IntrinsicElements['transformNode']> & {
      size?: number;
    }
  ) {
    const scene = useScene();
    const emitter = useRef<TransformNode>(null);

    useEffect(() => {
      if (!scene) return;

      const particleSystemName = `${props.name}.particeSystem`;
      const capacity = 100000;
      const rate = 10000;
      const emitterSize = 0.005;
      const particleSystem = GPUParticleSystem.IsSupported
        ? new GPUParticleSystem(particleSystemName, { capacity: capacity }, scene)
        : new ParticleSystem(particleSystemName, capacity, scene);

      var noiseTexture = new NoiseProceduralTexture(`${props.name}.noise`, 256, scene);
      noiseTexture.animationSpeedFactor = 2;
      noiseTexture.persistence = 0.5;
      noiseTexture.brightness = 0.5;
      noiseTexture.octaves = 2;

      particleSystem.emitter = emitter.current ?? null;
      particleSystem.emitRate = rate;
      particleSystem.minEmitPower = -0.02;
      particleSystem.maxEmitPower = -0.05;
      particleSystem.gravity.y = 0.1;

      particleSystem.particleEmitterType = new SphereParticleEmitter(emitterSize);
      particleSystem.particleTexture = new Texture(flareTexture, scene);
      particleSystem.color1 = Color4.FromColor3(Color3.Red());
      particleSystem.color2 = Color4.FromColor3(Color3.Yellow());
      particleSystem.colorDead = Color4.FromColor3(Color3.Black());

      particleSystem.minLifeTime = 1;
      particleSystem.maxLifeTime = 2;
      particleSystem.minSize = 0.001;
      particleSystem.maxSize = 0.002;

      // particleSystem.noiseTexture = noiseTexture;
      // particleSystem.noiseStrength = new Vector3(1, 1, 1);

      particleSystem.disposeOnStop = true;
      particleSystem.start();

      return () => {
        particleSystem.stop();
      };
    }, [props.name, scene]);

    useEffect(() => {
      if (!scene) return;

      let alpha = 0;
      const size = (props.size ?? 1) * 0.5;
      const speed = 0.001;

      const update = () => {
        if (!scene.deltaTime) return;
        if (!emitter.current) return;
        emitter.current.setPositionWithLocalVector(
          new Vector3(size * Math.cos(alpha), 0, size * Math.sin(alpha))
        );
        alpha += scene.deltaTime * speed;
      };

      scene.registerBeforeRender(update);

      return () => {
        scene.unregisterBeforeRender(update);
      };
    }, [props.size, scene]);

    return (
      <>
        <transformNode {...props}>
          <transformNode name={`${props.name}.emitter`} ref={emitter} />
        </transformNode>
      </>
    );
  });

  const UnregisteredThings = memo(function UnregisteredThings() {
    const unregisteredThings = useRecoilValue(xrThingsState).filter(
      (t) => t.status === 'unregistered'
    ) as XrThingUnregistered[];

    return (
      <>
        {unregisteredThings.map(({ tracked }) => {
          const key = `unregistered_${tracked.id}`;
          return (
            <NewDeviceMarker
              key={key}
              name={key}
              size={tracked.realWorldWidth}
              setPreTransformMatrix={[tracked.transformationMatrix]}
            />
          );
        })}
      </>
    );
  });

  const RegisteredThings = memo(function RegisteredThings() {
    const registeredThings = useRecoilValue(xrThingsState).filter(
      (t) => t.status === 'registered'
    ) as (XrThing & XrThingRegistered)[];

    return (
      <>
        {registeredThings.map(({ id, name, tracked, marker }) => {
          const key = `registered_${id}_${name}`;
          switch (name) {
            case 'Light':
              return (
                <sphere
                  key={key}
                  name={key}
                  diameter={0.1}
                  setPreTransformMatrix={[tracked.transformationMatrix]}
                />
              );
            case 'Air Conditioner':
              return (
                <box
                  key={key}
                  name={key}
                  size={0.1}
                  setPreTransformMatrix={[tracked.transformationMatrix]}
                />
              );
            default:
              return (
                <torusKnot
                  key={key}
                  name={key}
                  radius={marker.estimatedRealWorldWidth * 0.5 * 0.5}
                  setPreTransformMatrix={[tracked.transformationMatrix]}
                />
              );
          }
        })}
      </>
    );
  });

  return (
    <Scene>
      <RecoilBridge>
        <hemisphericLight name="light1" intensity={0.7} direction={Vector3.Down()} />
        <arcRotateCamera
          name="camera"
          alpha={Math.PI * 0.5}
          beta={Math.PI * 0.5}
          radius={1.5}
          target={Vector3.Zero()}
        />
        <XrExperience />
        <Suspense>
          <UnregisteredThings />
        </Suspense>
        <Suspense>
          <RegisteredThings />
        </Suspense>
      </RecoilBridge>
    </Scene>
  );
});

/**
 * A Remote / AR Remote
 *
 * [관련 이슈 #133](https://github.com/zigbang/smarthome-app/issues/133)
 */
export const Aremote = memo(function Aremote() {
  return (
    <RecoilRoot>
      <div className={styles.fullscreen}>
        <Engine antialias adaptToDeviceRatio>
          <AremoteScene />
        </Engine>
        <div id="WebXrUi">
          <Suspense>
            <BottomSheet />
          </Suspense>
        </div>
      </div>
    </RecoilRoot>
  );
});
